import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { PlayerClientService } from '@reveldigital/player-client';
import { map } from 'rxjs/operators';
import * as moment from 'moment-timezone';

@Injectable({
  providedIn: 'root'
})
export class CalendarDataService {

  // DEVELOPMENT VARAIBLES

  // private url = 'https://glacial-hollows-70580.herokuapp.com/ical/2020-10-01T00:00:00.000Z/2020-10-05T23:00:00.000Z?url=https://calendar.google.com/calendar/ical/nn7p43qvv93aum1r96um2jb25c%40group.calendar.google.com/public/basic.ics'

  private TZName: any;
  private urlPref = new gadgets.Prefs().getString('url');
  private url = `https://glacial-hollows-70580.herokuapp.com/ical/2020-09-29T00:00:00.000Z/2020-10-15T23:00:00.000Z?url=${this.urlPref}`;


  constructor(private http: HttpClient, private client: PlayerClientService) {}

  getEvents(): Observable<any> {

    this.client.getDeviceTimeZoneName().then((res) => {
      console.log('Time Zone Name: ' + res);
      this.TZName = res;
    });

    this.client.getDeviceTimeZoneID().then((res) => {
     console.log('Time Zone Id: ' + res);

    });

    this.client.getDeviceTimeZoneOffset().then((res) => {
      console.log('Time Zone Offset: ' + res);

    });

    return this.http.get<any>(this.url).pipe(
      map(data => {

        moment.tz.setDefault(this.TZName);

        let eventObj = new Object();
        let location: string;
        let summary: string;
        let startDate: Date;
        let endDate: Date;
        let statContainer: any;
        let eventContainer: any;
        const tempArr = [];
        let timeZone: string;



        if (data.events.length > 0){
            console.log('YES');
          }else{
            eventObj = {
              location: 'fargo',
              summary: 'my meeting',
              startDate: moment.tz(this.TZName).toDate(),
              endDate: moment.tz(this.TZName).toDate(),
              timeZone: this.TZName
            };

            statContainer = eventObj;
            tempArr[0] = statContainer;
          }

        if (data.occurrences.length > 0){
            eventContainer = data.occurrences.reduce((result: any, event: any) => {

              startDate = moment.tz({
                year : event.startDate.year,
                month : event.startDate.month - 1,
                day : event.startDate.day,
                hour : event.startDate.hour,
                minute : event.startDate.minute,
                second : event.startDate.second,
                timezone: event.startDate.timezone
              }, this.TZName).toDate();

              endDate = moment.tz({
                year : event.endDate.year,
                month : event.endDate.month - 1,
                day : event.endDate.day,
                hour : event.endDate.hour,
                minute : event.endDate.minute,
                second : event.endDate.second,
                timezone: event.startDate.timezone
              }, this.TZName).toDate();

              event.item.component[1].map((item: any) => {
                if (item[0] === 'location'){
                  location = item[3];
                }
                if (item[0] === 'summary'){
                  summary = item[3];
                }
                eventObj = {
                  location,
                  summary,
                  startDate,
                  endDate,
                  timeZone
                };
              });
              result.push(eventObj);

              return result;
            }, []);
          }
        const finalList = tempArr.concat(eventContainer).sort((a, b) => {
            return a.startDate - b.startDate;
          });
        console.log(finalList);
        console.log(this.TZName);
        return finalList;
      })
    );
  }
}
