import { CssSelector } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { CalendarDataService} from '../Services/calendar-data.service';
import * as moment from 'moment-timezone';
import { MatTableDataSource } from '@angular/material/table';
import { PlayerClientService } from '@reveldigital/player-client';

@Component({
  selector: 'app-default-table',
  templateUrl: './default-table.component.html',
  styleUrls: ['./default-table.component.css']
})

export class DefaultTableComponent implements OnInit {

  displayedColumns: string[] = ['when', 'where', 'summary'];
  dataSource: any;
  value: any;
  headerStyleObj = {};
  cellStyleObj = {};
  countDown = false;
  events: any;

  textStyleHeader = new gadgets.Prefs().getString('fontStyleHeader');
  textStyleCell = new gadgets.Prefs().getString('fontStyleCell');

  // DEVELOPMENT VARIABLES

  //textStyleHeader = 'color: red; font-size: large';
  //textStyleCell = 'color: red; font-size: large';

  constructor(private calendarService: CalendarDataService, private client: PlayerClientService) { }

  ngOnInit(): void {
    this.getEvents();
    this.getHeaderStyle();
    this.getCellStyle();
    setInterval(() => this.updateEvents(), 5000);
  }

  updateEvents(): void {
    if (!this.events) { return; }



    this.events.forEach((element, index) => {
      moment.tz.setDefault(element.timeZone);

      const diff = ((moment(element.startDate).valueOf() - moment().valueOf()));

      if (diff < 1000 * 1000 * 3.6) {
        element.countDown = moment(element.startDate).fromNow();
      }

      if (diff < 0){
        this.events.splice(index, 1);
      }
    });
    this.dataSource = new MatTableDataSource(this.events);
  }

  getHeaderStyle(): void {
    this.textStyleHeader.split(';').forEach( a => {
      const styles = a.split(':');
      if (styles[0]){
        this.headerStyleObj[styles[0].trim()] = styles[1].trim();
      }
    });
  }

  getCellStyle(): void {
    this.textStyleCell.split(';').forEach( a => {
      const styles = a.split(':');
      if (styles[0]){
        this.cellStyleObj[styles[0].trim()] = styles[1].trim();
      }
    });
  }

  getEvents(): void {
    const eventList = this.calendarService.getEvents()
      .subscribe(events =>  {
        this.events = events;
        this.updateEvents();
      });
  }
}
